  font-weight: 700;\n  background-color: #C8DBBE;\n  border-top-right-radius: 5px;\n  border-bottom-right-radius: 5px;\n}\n/* .item-info > span {\n  padding-bottom: 100px;\n} */\n@media screen and (max-width: 1199px) {\n  .item-row{\n    width: 100%;\n    padding: 10px;\n    /* display: none; */\n  }\n\n  .fan-art-item {\n    width: 100%;\n    text-align: center;\n    max-height: 200px;\n    object-fit: cover;\n    object-position: center;\n  }\n}\n\n// list item\n\n/* movie item */\n\n.item-item {\n  width: 100%;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  overflow: hidden;\n}\n\n.item-item__header {\n  position: relative;\n}\n\n.item-item .item-item__header .item-item__header__poster {\n  width: 100%;\n}\n\n.item-item .item-item__header .item-item__header__rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  left: 0;\n  display: inline-block;\n  background-color: black;\n  color: white;\n}\n\n.item-item .item-item__header .item-item__header__rating .item-item__header__rating__score {\n  margin-left: 16px;\n}\n\n.item-item .item-item__content {\n  padding: 16px;\n}\n\n.item-item .item-item__content h3 {\n  margin: 0 0 10px 0;\n}\n\n.item-item .item-item__content h3 a {\n  color: #db0000;\n  text-decoration: none;\n}\n\n.item-item .item-item__content p {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  -webkit-box-orient: vertical;\n}\n\n/* item */\n\n.item {\n  margin : 0 auto;\n  width: 100%;\n  max-width: 800px;\n  // display: grid;\n  // grid-template-columns: 1fr;\n  // gap: 18px 16px;\n}\n\n.item .item__poster {\n  width: 100%;\n  // max-width: 400px;\n}\n\n.item .item__info {\n  margin-top: 20px;\n}\n\nh4 {\n  margin-bottom: 10px;\n}\n\n.review {\n  margin-top: 30px;\n  .item-review {\n    .item__name {\n      font-weight: 700;\n    }\n    .item__review_date {\n      color: #CCCCCC;\n    }\n  }\n}\n\n.title-review{\n  width: 100%;\n  margin-bottom: 10px;\n}\n\n.item__title {\n  padding: 10px;\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.item-info > h2 > a {\n  min-height: 44px;\n  min-width: 44px;\n  padding: 12px;\n}\n\n@media screen and (min-width: 650px){\n  .item {\n    grid-template-columns: auto 1fr;\n  }\n  .item .item__overview {\n    grid-column-start: 1;\n    grid-column-end: 3;\n}\n}\n\n\n/*\n  Like\n*/\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n// form review\n\nform {\n  margin: 2em 0;\n  width: 100%;\n  max-width: 800px;\n  padding: 1.5em;\n  border: 3px dotted var(--btn-hover);\n  border-radius: 0 15px 0 15px;\n}\n\n.form-control {\n  display: block;\n  width: 100%;\n  min-height: 1em;\n  padding: 0.3em;\n  font-size: 1em;\n  font-weight: normal;\n  color: var(--font-color);\n  background-color: #fff;\n  appearance: none;\n  outline: none;\n  border: 1px solid transparent;\n  border-bottom: 1px solid #f0e3ca;;\n}\n\n.form-control:hover {\n  border-bottom: 1px solid  #ff8303;\n}\n\n.form-label {\n  padding-bottom: 10px;\n  font-weight: bold;\n  font-style: normal;\n  font-size: small;\n  color: '#000000';\n}\n\n.mb-3 {\n  margin-bottom: 1.3em;\n  margin-top: 0.5em;\n}\n\n.submit-btn {\n  padding: 0.5em 1em;\n  font-weight: 700;\n  color: white;\n  text-align: center;\n  vertical-align: middle;\n  background-color: #ff8303;\n  border: 1px solid transparent;\n  font-size: large;\n  cursor: pointer;\n  text-decoration: none;\n}\n\n"],"sourceRoot":""}]);
